.movies .movie {
  width: 45%;
  display: flex;
  align-items: flex-start;
  margin-bottom: 5rem;
  justify-content: space-between;
  background-color: white;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.movie_poster {
  position: relative;
  top: -40px;
  width: 100%;
  max-width: 100px;
  margin: 0 auto;
  box-shadow: rgba(0, 0, 0, 0.55) 0px 5px 15px;
}

.movie_data {
  padding: 1.2rem;
  width: 70%;
}

ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  padding: 0;
  flex-wrap: wrap;
}
li {
  margin-left: 1rem;
  color: lightgray;
}
